.Dasboard {
    display: grid;
    height: 1fr;
    grid-template-areas: "Contadores Contadores Contadores Contadores" "Equilibrio Dolar Dolar Dolar" "Utilidad Dolar Dolar Dolar";
}

.Contadores {
    grid-area: Contadores;
    display: grid;
    gap: 1rem;
    grid-auto-flow: dense;
    grid-auto-rows: 10rem;
    grid-template-columns: repeat(auto-fill, minmax(12rem, 1fr));
}

.Equilibrio {
    grid-area: Equilibrio;
}

.Utilidad {
    grid-area: Utilidad;
}

.Dolar {
    grid-area: Dolar;
    display: grid;
    gap: 1rem;
    grid-auto-flow: dense;
    grid-auto-rows: 38px 3fr;
    grid-template-columns: repeat(auto-fit, 100%);
}